.popup__title {
  font-family: 'Inter', 'Arial', 'Helvetica', sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  margin: 0;
  margin-bottom: 42px;
  width: 100%;
  max-width: 330px;
}

@media screen and (max-width: 480px) {
  .popup__title {
    font-size: 18px;
    line-height: 22px;
    margin-bottom: 69px;
  }
}